<template>
    <div style="width:100%;height: 95%;margin-top: 20px;">
        <div class="ssix">
            <div class="six">
                <img src="../../../assets/1.png" alt="">
                <h1>{{ li.insnum }}</h1>
                <p>子企业</p>
            </div>
            <div class="six">
                <img src="../../../assets/2.png" alt="">
                <h1>{{ li.maxnum }}</h1>
                <p>最大坐席数</p>
            </div>
            <div class="six">
                <img src="../../../assets/3.png" alt="">
                <h1>{{ li.num }}</h1>
                <p>实际坐席数</p>
            </div>
            <div class="six">
                <img src="../../../assets/4.png" alt="">
                <h1>{{ li.stopnum }}</h1>
                <p>停用坐席数</p>
            </div>
            <div class="six">
                <img src="../../../assets/5.png" alt="">
                <h1>{{ li.maxconcurrency }}</h1>
                <p>最大登录并发总数</p>
            </div>
            <div class="six">
                <img src="../../../assets/6.png" alt="">
                <h1>{{ li.yucenum }}</h1>
                <p>预测式外呼坐席并发总数</p>
            </div>
        </div>
        <div class="smain">
            <div style="display: flex;justify-content: space-between;">
                <p>
                    超级企业信息<i class="el-icon-edit" style="margin-left: 10px;font-size: 20px;color: #91A1A9;"></i>
                </p>
                <p style="color: #91A1A9;">
                    当前超级企业为{{ st }} <a href="" style="color: #229DFF;" @click.prevent="updasta">{{ tq }}</a>
                </p>
            </div>
            <div class="smain-nier" style="display: flex;justify-content:space-between">
                <div class="tex">
                    <div class="tex-le">
                        <p><span>*</span> 超级企业名称</p>
                        <p><span>*</span> 企业域名</p>
                        <p><span>*</span> 超级管理员账号邮箱</p>
                        <p><span>*</span> 邮箱</p>
                    </div>
                    <div class="tex-ri">
                        <p>{{ li.ename }}</p>
                        <p>{{ li.ipstr }}</p>
                        <p>{{ li.adminname }}</p>
                        <p>{{ li.adminemil }}</p>
                    </div>
                </div>
                <div class="tex">
                    <div class="tex-le">
                        <p>超级企业ID</p>
                        <p>最大坐席数</p>
                        <p>最大登录并发总数</p>
                        <p>创建时间</p>
                    </div>
                    <div class="tex-ri">
                        <p>{{ li.id }}</p>
                        <p>{{ li.maxnum }}</p>
                        <p>{{ li.maxconcurrency }}</p>
                        <p>{{ time }}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { getuseros, updasta } from "../../../api/https";

export default {
    data() {
        return {
            li: {},
            st: '启用状态',
            tq: '停用超级企业',
            time: '',
        }
    },
    watch: {
        s() {
            if (this.li.status == 0) {
                this.st = '停用状态'
                this.tq = '启用超级企业'
            } else {
                this.st = '启用状态'
                this.tq = '停用超级企业'
            }
        }
    },
    computed: {
        s() {
            if (this.li.status == 0) {
                return false
            } else {
                return true
            }
        }
    },
    created() {
        this.lli()
        this.$eventBus.$on('route-change', this.lli)
    },
    methods: {
        lli() {
            getuseros(Object.keys(JSON.parse(localStorage.getItem('id')))[0]).then(res => {
                this.li = res.data
                var tim = new Date(res.data.create_time * 1000)
                this.time = tim.getFullYear() + '-' + (tim.getMonth() + 1) + '-' + tim.getDate() + ' ' + tim.getHours() + ':' + tim.getMinutes() + ':' + tim.getSeconds()
            })
        },
        updasta() {
            if (this.li.status == 0) {
                updasta(this.li.id, 1).then(res => {
                    console.log(res);
                    this.lli()
                })
                this.$emit('child-event')
            } else if (this.li.status == 1) {
                this.$confirm('停用超级企业，其下子企业也全部停用!', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    updasta(this.li.id, 0).then(res => {
                        console.log(res);
                        this.lli()
                    })
                    this.$message({
                        type: 'success',
                        message: '停用成功!'
                    });
                    this.$emit('child-event')
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消停用'
                    });
                })
            }
        }
    }
}
</script>
<style scoped>
.ssix {
    width: 100%;
    border: 1px solid #E9F3FB;
    display: flex;
    justify-content: space-between;
    padding: 40px 0px;
    box-sizing: border-box;
}


.six {
    width: 14%;
    height: 100%;
    text-align: center;
}

.six h1 {
    font-size: 30px;
    margin: 0;
    line-height: 50px;
    font-weight: 500;
}

.six p {
    margin: 0;
    color: #91A1A9;
}

.smain {
    width: 100%;
    border: 1px solid #E9F3FB;
    box-sizing: border-box;
    margin-top: 20px;
    padding: 10px;
}

.tex {
    width: 450px !important;
    display: flex;
}

.tex p {
    line-height: 25px;
}

.tex span {
    color: red;
}

.tex-le {
    width: 50%;
    text-align: right;
    padding: 10px;
}

.tex-le p {
    color: #91A1A9;
}

.tex-ri {
    width: 50%;
    text-align: left;
    padding: 10px;
}
</style>